import * as React from 'react';
import { useNavigate } from 'react-router-dom';

import Staff from '@miq/staffjs';

import { ProductCreateForm } from './forms';
import { TProduct } from '../types';

export default function ProductCreateView() {
  const nav = useNavigate();
  return (
    <Staff.View title="Ajouter un produit" requiredPerms={['store.add_product']} back="../">
      <Staff.Section>
        <ProductCreateForm onSuccess={({ data }: { data: TProduct }) => nav(`../${data.slug}`)} />
      </Staff.Section>
    </Staff.View>
  );
}
